import React, { Component } from 'react';
// import axios from 'axios';
// import { parseString } from 'xml2js';
import { Card, CardText, CardBody, CardTitle, CardSubtitle } from 'reactstrap';

import logo from './logo.svg';

import 'bootstrap/dist/css/bootstrap.css';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
      this.state = {
          items: [],
          title: '',
          description: '',
          success: false
      };
  }

  componentDidMount() {
    // let _this = this;
    // axios.get(process.env.REACT_APP_RSS_FEED_URL, {
    //   headers: {
    //     'Access-Control-Allow-Origin': '*',
    //     'Content-Type': 'text/plain'
    //   }, 
    //   cors: true
    // }).then(response => {            
    //     parseString(response.data, (err, result) => {
    //       console.log(result.rss.channel[0].item[0]);
    //       _this.setState({
    //         items: result.rss.channel[0].item,
    //         title: result.rss.channel[0].title,
    //         description: result.rss.channel[0].description,
    //         success: true
    //       });
    //     });
    //   });
    let xhr = this.createCorsRequest('GET', process.env.REACT_APP_RSS_FEED_URL);
    if(!xhr) {
      console.log('CORS not supported');
    } else {
      xhr.setRequestHeader('Access-Control-Allow-Origin', '*');
      xhr.withCredentials = false;

      xhr.onload = () => {
        console.log(xhr.responseText);
      };

      xhr.onerror = () => {
        console.log('xhr error');
      }
  
      xhr.send();
    }    
  }

  createCorsRequest(method, url) {
    let xhr = new XMLHttpRequest();
    if("withCredentials" in xhr) {
      xhr.open(method, url, true);
    } else if(typeof XDomainRequest !== "undefined") {
      xhr = new XDomainRequest();
      xhr.open(method, url);
    } else {
      xhr = null;
    }

    return xhr;
  }

  render() {

    const items = this.state.items.map((item, index) => 
      <div className="col-md-3 mb-4">
        <Card key={index}>
          <CardBody>
            <CardTitle><a href={item.link} target="_blank">{item.title}</a></CardTitle>
            <CardSubtitle>{item.pubDate}</CardSubtitle>
            <CardText>{item.description}</CardText>
          </CardBody>
        </Card>
      </div>
    );

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">{this.state.title}</h1>
          <small>{this.state.description}</small>
        </header>
        <div className="App-body">
          <div className="row mt-4 px-4">
            {items}
          </div>
        </div>
      </div>
    );
  }
}

export default App;



